.header__logo {
  background: no-repeat url('../../../images/logo.svg');
  width: 142px;
  height: 33px;
  margin-bottom: 41px;
}

@media screen and (max-width: 990px) {
  .header__logo {
    margin-left: 5%;
  }
}

@media screen and (max-width: 425px) {
  .header__logo {
    width: 103.74px;
    height: 24.4px;
    background-size: contain;
    margin-bottom: 31.6px;
    margin-left: 8.3%;
  }
}
